import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import { Helmet } from "react-helmet"
import { css } from "@emotion/core"

export default ({ data }) => (
  <Layout>
    <Helmet title={data.site.siteMetadata.title} defer={false} />
    <h1>About {data.site.siteMetadata.title}</h1>
    <div
        css={css`
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,
          -50%);
          display: flex;
          flex-direction: column;
          align-items: center;
        `}
      >
        <div
          css={css`
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            width: 140px;
            height: 50px;
            background: #1d4c86;
          `}>
        </div>
        <div
          css={css`
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            width: 140px;
            height: 50px;
            background: #1d8086;
          `}>
        </div>
    </div>
    <p>
      Docunext is a blog by me, Albert Lash, featuring a collection of technical
      notes I have collected over a decade of working with mostly open source,
      networking and server software.
    </p>
  </Layout>
)

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
